eleccionan de modo inconsciente lo que consideran más relevante para ellos en ese momento; se ve lo que se quiere. ")])])])]),_c('li',{staticClass:"mb-0 mt-2"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-1 text-align-right p-0"},[_c('i',{staticClass:"fas fa-angle-right"})]),_c('div',{staticClass:"col-11"},[_c('p',[_c('strong',[_vm._v("Árbitro de conflictos:")]),_vm._v(" la vista es el sentido que más activa el proceso cognitivo y participa en el proceso de aprendizaje. De este modo, si lo que se ve contradice lo que se siente, se escucha o se huele, se produce una disonancia cognitiva en el individuo; en los sentidos se tiende a priorizar el sentido de la vista como el predominante, esta peculiaridad implica que la vista suele tener la última palabra en su interacción con otros sentidos.")])])])])])])])]),_c('div',{attrs:{"titulo":"Olfato"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/template/tema-1-49.png"),"alt":"Texto que describa la imagen"}})]),_c('p',{staticClass:"mt-4"},[_vm._v("La fuerza evocadora de los olores era mucho más intensa; el olfato se conoce también como el sentido silencioso, por lo difícil que resulta describir con palabras los olores o los sentimientos de bienestar que muchas veces llevan asociados. El marketing olfativo contempla dos cualidades básicas de los aromas para evaluar su uso: placer y congruencia; la primera, el placer, recoge la experiencia intrínseca del olor, mientras que la congruencia conecta el olor con el contexto: tienda, producto o marca "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("En el caso del olfato, estudios adelantados permiten concluir que las sensaciones olfativas producen recordación y evocación de lugares, espacios y, por supuesto, de marcas de productos.")]),_c('p',{staticClass:"mt-4"},[_vm._v("Existen productos que por su propia estructura son constituidos por fragancias y aromas que se convierten en sus principales atributos, el aroma de una libra de café o de uno ya preparado, los alimentos, los cosméticos y los perfumes son ejemplos de productos que traen consigo un atributo diferenciador de vital importancia en la estrategia de producto; pero hay otros productos cuyo aroma o fragancia no hace parte de las características de mayor trascendencia y que pueden ser susceptibles de trabajarse, incluso en ítems cuya relación con el olfato podría estar bastante lejana, tal es el caso de la marca de zapatos infantiles Bubble Gummers, en la que se introdujeron materiales con un olor característico, muy parecido al chicle, lo que hace que nadie se resista a ver estos zapatos exhibidos sin acercarlos a su nariz, casi como efecto reflejo "),_c('strong',[_vm._v("(Martínez, 2018).")])]),_c('p',{staticClass:"mt-4"},[_c('strong',[_vm._v("Martínez (2018)")]),_vm._v(" describe en la tabla 4 algunos aromas con su efecto a partir de estudios de la marca española Aromarketing.")])])]),_vm._m(70),_vm._m(71),_vm._m(72),_c('AcordionA',{staticClass:"mt-5",attrs:{"tipo":"b","clase-tarjeta":"tarjeta tarjeta--gris"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Audición"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/template/tema-1-51.png"),"alt":"Texto que describa la imagen"}})]),_c('p',{staticClass:"mt-4"},[_vm._v("El sentido del oído está constantemente activo, desde que se nace hasta que se muere, todos los días del año, durante las veinticuatro horas del día; los oídos trabajan también mientras se duerme, aunque no se esté consciente de ello "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("Los sonidos están íntimamente ligados a la vida cotidiana, desde el momento de despertar se vive y se interactúa con diversidad de sonidos que producen, en su mayor parte, identificaciones y, por lo tanto, evocaciones de diferente índole. El sonido de la identificación de ciudad está relacionado con sonidos de automóviles, pitos y muchos sonidos agregados, los cuales transmiten sensaciones de estrés; caso contrario sucede con la identificación del campo, relacionado con la placidez y que se da a través de sonidos de pájaros, brisa, agua y mar, los cuales logran definitivamente proporcionar tranquilidad "),_c('strong',[_vm._v("(Martínez, 2018).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("Es importante que esa creación de audiotipos y búsqueda de la música y sonidos más adecuados esté relacionada íntimamente con el trabajo visual y olfativo que se realice, al tener en cuenta que cada espacio donde interactúe el producto es diferente y, por lo tanto, susceptible de ser revisado para su aplicación puntal, un producto tendrá diferentes relacionamientos con sus clientes por diversos aspectos, como ubicación, clima, cultura, espacio y demás que afecten de manera clara la experiencia "),_c('strong',[_vm._v("(Martínez, 2018).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("Es habitual escuchar música de fondo en los establecimientos comerciales. Las voces de la megafonía anunciando promociones en los hipermercados captan la atención con cierta frecuencia mientras las personas llenan el carro de la compra, y cada vez más a menudo se enfrentan a las voces de los contestadores automáticos que repiten sus mensajes con voz impersonal "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("Evocador de recuerdos. El ser humano posee la capacidad de asociar sensaciones procedentes de los cinco sentidos y relacionarlas con conceptos e ideas, que a su vez generan sentimientos y emociones que le recuerdan sus experiencias vitales. El oído tiene un efecto inmediato sobre el recuerdo; se sabe que la fuerza evocadora de la música es capaz de transportar a las personas a lugares y dimensiones temporales remotas de la vida. Los sonidos forman parte natural del entorno humano y crean asociaciones o evocan recuerdos que los responsables de marketing pueden integrar de forma muy rentable en sus estrategias de marketing sensorial "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("La voz es otro elemento importante para el marketing de los sentidos, es un sonido muy poderoso; la voz, al igual que las huellas dactilares, es única e irrepetible. Las voces elegidas para los anuncios televisivos y de radio son seleccionadas cuidadosamente para transmitir seguridad, sensualidad, acción o cercanía. La voz tiene la cualidad de permitir expresar mucho más de lo que se dice con simples palabras; existe un metalenguaje que transmite las emociones, el contexto o las intenciones, que puede alterar el sentido de lo que se dice mediante la ironía o el sarcasmo, o modulando el tipo de voz –seria, enfadada, cariñosa– "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("La música de fondo ha sido utilizada tradicionalmente para amenizar el punto de venta. La importancia de la música que suena en un establecimiento radica en la influencia que ejerce en la conducta del consumidor; que compre más, que compre menos, que alargue su estancia en una tienda o que salga de ella antes de haber terminado su compra. El tempo, el ritmo, los instrumentos o la novedad de la pieza influyen en el estado de ánimo de quien escucha; la música energética con tempo rápido provoca más sentimientos positivos que la música lenta y sedante.")])]),_c('div',{attrs:{"titulo":"Gusto"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/template/tema-1-52.png"),"alt":"Texto que describa la imagen"}})]),_c('p',{staticClass:"mt-4"},[_vm._v("Es el más íntimo de todos los sentidos del ser humano, en la medida en que implica el contacto interior, directo y, durante cierto tiempo, del individuo con el producto en la boca. Para conseguir este contacto el producto tiene que pasar por la totalidad del resto de los sentidos, que actúan como filtros; una inspección táctil a través de los labios y de la propia lengua, que evalúan y mandan información al cerebro sobre la consistencia del producto a probar, su sabor, textura y temperatura "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("Se habla de cuatro sabores básicos: dulce, salado, ácido y amargo. Los otros sentidos son condicionantes del sentido del gusto, así como el efecto que pueden ejercer la publicidad, la marca comercial y las connotaciones que esta tiene: el nombre del plato en la carta de un restaurante, los ingredientes que figuran en el envase de un producto, etc. Una etiqueta que indique un alto contenido en grasa para un producto transmite una mayor percepción de sabor, e incluso un producto con una fecha de caducidad más próxima que otro va a percibirse como menos sabroso. El gusto está muy influido por factores ambientales y sociales; por el entorno más cercano, por el motivo de la comida, por la gente con quien se comparte, es decir, la implicación emocional y la predisposición con que se aborda "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("Degustaciones de productos en el punto de venta. Dado el enorme poder emocional que tiene el sentido del gusto es evidente que cuando se habla de productos de alimentación, la prueba de producto en la tienda es un elemento importante para generar conocimiento y aceptación de este. Cuando se trata de detallistas especializados en una determinada categoría de productos de alimentación, casi siempre se utiliza la degustación dentro del punto de venta como una herramienta de atracción para incentivar la compra "),_c('strong',[_vm._v("(Manzano, 2012).")])])]),_c('div',{attrs:{"titulo":"Tacto"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/template/tema-1-53.png"),"alt":"Texto que describa la imagen"}})]),_c('p',{staticClass:"mt-4"},[_vm._v("Basta con observar detenidamente al consumidor mientras compra para comprobar la importancia que para el ser humano tiene el sentido del tacto; tocar un producto supone un medio fundamental para generar la información o emoción que una vez integrada como percepción en el comportamiento del consumidor facilite la decisión de compra "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("La influencia del tacto no se reduce a afectar al resto de los sentidos. El contacto directo y casual entre personas provoca sensación de proximidad, afecto y calidez en su destinatario, afectando positivamente tanto a la valoración de los estímulos en los que se produce ese contacto, como a una mejor valoración del prestatario. El contacto supone una oportunidad para la venta personal y una forma de diferenciarse de la venta online; por su parte, la venta online debe compensar su despersonalización, ya sea por medio de servicios de asistencia personal online o telefónica, o a través de personajes automatizados "),_c('strong',[_vm._v("(Manzano, 2012).")])]),_c('p',{staticClass:"mt-4"},[_vm._v("Los beneficios que proporciona el tacto al consumidor son múltiples. El primero permite aportar valor a los distintos parámetros físicos de un producto en relación con una necesidad del cliente; facilita información directa, obtenida personalmente por el comprador, sobre aquellos elementos físicos en los que el consumidor necesita saber más para generar una percepción que facilite su decisión. Este factor es fundamental especialmente en aquellos productos en los que la calidad puede valorarse a partir de esta inspección táctil, por lo que se convierte en la principal fuente para tomar una decisión, por encima de cualquier otra vía de comunicación. El acceso a un producto refuerza la confianza en la evaluación hecha sobre este y, en la decisión de compra realizada también funciona en el sentido contrario; un deficiente acceso al producto en un punto de venta genera una información incompleta, y sobre todo genera desconfianza por parte del consumidor, tanto en su valoración como en el propio punto de venta "),_c('strong',[_vm._v("(Manzano, 2012).")])])])]),_vm._m(73),_vm._m(74),_vm._m(75),_vm._m(76),_vm._m(77),_vm._m(78),_vm._m(79),_vm._m(80),_vm._m(81),_vm._m(82),_vm._m(83),_vm._m(84),_c('AcordionA',{staticClass:"mt-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--gris"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Eventos permanentes: "}},[_c('div',{staticClass:"col-4 d-none d-md-block px-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/template/tema-1-62.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-8"},[_c('p',[_vm._v("Son aquellos eventos de merchandising en los que el surtido se presenta de manera continua e indefinida, es decir, son constantes durante todo el año y se encuentran siempre dentro del punto de venta; se realizan para productos que no tienen una gran estacionalidad y que no están en promoción. Los productos se presentan, por lo general, en góndolas, murales y mostradores, y en algunas ocasiones en sitios especiales, islas o montoneras, dependiendo del tipo de establecimiento, producto y estrategia empresarial.")])])]),_c('div',{attrs:{"titulo":"Eventos de temporada: "}},[_c('div',{staticClass:"row",attrs:{"titulo":"Eventos de temporada: "}},[_c('div',{staticClass:"col-4 d-none d-md-block px-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/template/tema-1-63.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-8"},[_c('p',[_vm._v("Son aquellos eventos de merchandising en los que el surtido se expone por un tiempo definido, es decir, durante algunos períodos del año y se encuentran en temporadas especiales en el punto de venta. Los productos que se manejan en este tipo de eventos son aquellos que tienen una elevada estacionalidad y su exhibición debe ser atractiva y de gran impacto para el comprador.")])])])]),_c('div',{attrs:{"titulo":"Eventos promocionales: "}},[_c('div',{staticClass:"row",attrs:{"titulo":"Eventos promocionales: "}},[_c('div',{staticClass:"col-4 d-none d-md-block px-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/template/tema-1-64.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-8"},[_c('p',[_vm._v("Son aquellos eventos de merchandising en los que el surtido se expone por un tiempo limitado, es decir, solo durante algunos períodos del año y que se encuentran en formas y momentos cortos en el punto de venta. Su propósito es atraer nuevos consumidores, mayor rentabilidad, equilibrar las temporadas, bajar existencias, realizar el lanzamiento de un producto, vender en cercanía a la fecha de vencimiento, realizar el cambio de referencias o de temporada y responder a la competencia del mercado. Se suelen utilizar obsequios o regalos en este tipo de eventos como: llaveros, gorras, calendarios, camisetas y muestras a menor escala de los productos promocionados, al buscar no solamente aumentar las ventas actuales, sino transformar a un nuevo cliente en el más fiel de los consumidores y, que la marca esté en contacto con el público.")])])])])]),_c('div',{staticClass:"h6 mt-5"},[_vm._v("Recursos de apoyo")]),_vm._m(85),_vm._m(86),_c('div',{staticClass:"h6 mt-5"},[_vm._v("Material P.O.P.: publicidad en el punto de venta")]),_vm._m(87),_vm._m(88),_vm._m(89),_vm._m(90),_vm._m(91),_vm._m(92),_vm._m(93),_vm._m(94),_vm._m(95),_vm._m(96),_vm._m(97),_vm._m(98),_vm._m(99),_vm._m(100),_vm._m(101),_vm._m(102),_vm._m(103),_vm._m(104),_c('p',{staticClass:"mt-5"},[_vm._v("Para la elaboración de un plan de acción de distribución se deben tener en cuenta los siguientes aspectos:")]),_vm._m(105)],1)],1)}
